@font-face {
	font-family: "Maven Pro";
    src: url(https://lindalor028.github.io/hmovie/assets/fonts/MavenPro-VariableFont_wght.ttf);
}

* {
    font-family: 'Maven Pro';
    color: white;
    text-decoration: none;
}

p {
    margin-left: 0rem;
}


h1 {
    // color: green;
    font-style: bold;
}

button {
    cursor: pointer;
}

.movie_mainview {
    display: grid;
    column-gap: 0.5rem;
    grid-template-columns: 2fr 1fr;
    width: auto;
}


  /* Make it responsive on small screens */
  @media (max-width: 768px) {
    .movie_mainview{
      grid-template-columns: 1fr; /* stack them vertically */
    }
  }


.actors_list {
    list-style: none;
    display: grid;
    margin-left: -2.5rem;
    grid-template-columns: 1fr 1fr 1fr 1fr  1fr;
    row-gap: 1rem;
    column-gap: 1rem;
}

.actors_list li {
    padding: 0.5rem;
    text-align: center;
}

.actors_list li:hover:not(.header) {
    background-color: rgb(53, 63, 83);
    border-radius: 1rem;
}
// Testing for video
.movie_videoview {
    display: grid;
    column-gap: 1rem;
    grid-template-columns: 0.8fr 2fr;
    width: auto;
}

.movie_videoview_title {
    display: grid;
    column-gap: 0.5rem;
    grid-template-columns: 5fr 1fr;
    width: auto;
}

.video_button {
    margin-top: 2rem;
    height: 50%;
}

#black_bg {
    position: fixed; /* Sit on top of the page content */
    display: none; /* Hidden by default */
    width: 100%; /* Full width (cover the whole page) */
    height: 100%; /* Full height (cover the whole page) */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5); /* Black background with opacity */
    z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
    cursor: pointer; /* Add a pointer on hover */
}

#movie_popup {
    width: 80%;
    height: 80%;
    position: fixed;
    top: 10%;
    left: 10%;
}

#movie_popup iframe {
    width: 100%;
    height: 100%;
}

#close_popup_button {
    position: fixed;
    left: 89%;
    top: 9%;
    z-index: 5;
    border-radius: 1rem;
    width: 2rem;
    height: 2rem;
    font-size: large;
}

.cast_movies {
    display: grid;
    column-gap: 1rem;
    row-gap: 2rem;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}

.cast_movies  a {
    padding:1rem;
}

.cast_movies a:hover:not(.header) {
    background-color: rgb(53, 63, 83);
    border-radius: 1rem;
}


// INCLUDES MOVIES
.movie_parts_list {
    display: grid;
    list-style: none;
    padding: 1rem;
    column-gap: 1rem;
    row-gap: 2rem;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    background-color:rgba(31,44,68,255);
    border-radius: 1rem;
}

.movie_parts_list a {
    padding: 0.5rem;
}

.movie_parts_list a:hover:not(.header) {
    background-color: rgb(53, 63, 83);
    border-radius: 1rem;
}

.movie_parts_img {
    height: 200px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
    background-color: black;
    border-radius: 0.5rem;
}


// HOME PAGE

.home_image {
    border-radius: 1rem;
    vertical-align: top;
    width: 100%;
    filter: brightness(50%);
    // mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
    mask-image: linear-gradient(black, transparent 50%);
}


.landing_items {
    position: relative;
    text-align: center;
    top: 50%;
    width: 100%;
    // height: 100%;
}


.text_over_image {
    color: white;
    position: absolute;  
    top: 15rem;
    width: 79%;
    text-align: center;
}

  

//EXPLORE
#movies_list {
    list-style-type: none;
    display: grid;
    background-color:rgba(31,44,68,255);
    grid-template-columns: repeat(auto-fit, minmax(min-content, 175px));
    column-gap: 1rem;
    row-gap: 1rem;
    padding: 0;
    margin: 0;
    padding: 2%;
    border-radius: 1rem;
    justify-content: center;
}
  
#movies_list li a{
    margin-top: -1px; /* Prevent double borders */
    padding: 12px;
    text-decoration: none;
    font-size: 18px;
    display: block
}
  
#movies_list li:hover:not(.header) {
    background-color: rgb(53, 63, 83);
    border-radius: 1rem;
}
  
.filter_list {
    list-style-type: none;
    width: 100%;
    text-indent: -10%;
}

.filter_list li input {
    width: 25px;
    height: 25px;
}

.filter_list li label {
    font-size: larger;
    vertical-align: 0.35rem;
}

.gray_box{ 
    background-color: rgb(53, 63, 83);
    border-radius: 1rem;
    padding: 2rem;
    height: fit-content;
}


// NEW STYLING (FIGMA)
.bg_blue {
    background-color: rgb(15, 20, 30);
}

body {
    margin: 0px;
}

.center_content {
    margin-left: 10%;
    margin-right: 10%;
    margin-bottom: 5%;
    padding: 0.5rem;
    min-height: calc(100vh - 267px);
    // background-color: antiquewhite;
}

.black_div_shadow {
    box-shadow: none 20px 10px 5px rgb(0, 0, 0);
}

#home_search {
    width: 60%;
    left: 40%;
