    top: 70%;
    left: 50%;
    transform: (translate(-50%, -50%));
    text-align: center;
}

#home_search input{
    color: black;
    width: 100%;
    height: 40px;
    background-image: url(/assets/images/search.jpeg);
    background-position: 10px center;
    background-repeat: no-repeat;
    background-size: 30px;
    border: 0;
    border-radius: 0.5rem;
}

#home_search input:active{
    color: black;
    width: 100%;
    height: 40px;
    // background-image: url(/assets/images/search.jpeg);
    // background-position: 10px center;
    // background-repeat: no-repeat;
    // background-size: 30px;
    border: 0;
    border-radius: 0.5rem;
}

.hmovie_title {
    font-size: 5rem;
    color: white;
    margin: 0%;
    // padding-top: 10rem;
}

.black_shadow {
    text-shadow: 0 0 10px #000000;
}

.sub_title {
    font-size: 3vw;
    margin:0%;
    width: 50%;
    margin-left: 25%;
    margin-bottom: 2rem;
}

.landing_div {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 36rem;
    margin-top: 3%;

    text-align: center;
    border-radius: 1rem;
    vertical-align: top;
    width: 100%;
    filter: brightness(70%);
    mask-image: linear-gradient(0deg, rgba(0,0,0,0) 0%, rgba(15,20,30,1) 20%);
}

.center_text {
    text-align: center;
}

.text_quote {
    font-size: 1.5rem;
    font-weight: bold;
}

.white_button {
    display: block;
    background-color:white;
    color: black;
    text-decoration: none;
    text-align: center;
    // font-weight:bld;
    padding: 1rem;
    width: 25%;
    border-radius: 0.5rem;
}

.center_button {
    width: 25%;
    margin: auto;
}

.site_footer {
    display: grid;
    padding-left: 10%;
    padding-right: 10%;
    padding-top: 1rem;
    padding-bottom: 1rem;
    margin-bottom: 0rem;
    height: fit-content;
    background-color: rgba(3,28,73,255);
    grid-template-columns: 2fr 1fr 1fr 1fr;
    column-gap: 3rem;
    box-shadow: none 20px 10px 5px rgb(0, 0, 0);
}

.sponsor_logos {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 1%;
    place-items: center;
    margin-top: 2rem;
}

.sponsor_logos_small{
    border-radius: 0.2rem;
    height: 100%;
    width: 100%;
    filter: brightness(80%);
}

.sponsor_logos_small:hover {
    filter: brightness(100%);
}

.sponsor_logos_big {
    border-radius: 0.2rem;
    height: 8vw;
    width: auto;
    margin-left: auto;
    margin-right: auto;
    filter: brightness(80%);
}

.sponsor_logos_big:hover {
    filter: brightness(100%);
}

.footer_sub_title {
    color: white;
    font-size: 20px;
    font-weight: bold;
}

.site_footer *{
    text-decoration: none;
}

.footer_stack_items {
    display:flex;
    flex-direction:column;
    row-gap: 1rem;
}

// HEADERS 
.site_header {
    display: grid;
    padding-left: 10%;
    padding-right: 10%;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    // height: 50px;
    background-color: rgba(3,28,73,255);
    grid-template-columns: 1fr 2fr 0.75fr 0.75fr 0.75fr;
    box-shadow: none 20px 10px 5px rgb(0, 0, 0);
    position: sticky;
    top: 0;
    z-index: 1;
}

.site_header * {
    color:white;
}

.site_header_nosearch {
    display: grid;
    padding-left: 10%;
    padding-right: 10%;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    height: 50px;
    background-color: rgba(3,28,73,255);
    grid-template-columns: 3fr 0.75fr 0.75fr 0.75fr;
    box-shadow: none 20px 10px 5px rgb(0, 0, 0);

    position: sticky;
    top: 0;
    z-index: 1;

}

.nav_button {
    display: block;
    color:rgb(204, 208, 214);
    text-decoration: none;
    padding-top: 15px;
    text-align: center;
}

.nav_button:hover {
    color: white;

}

.home_button {
    text-decoration: none;
    font-size: 250%;
}

